//* Import File
import React from 'react'
import ReactPlayer from 'react-player'
import '../css/Portofolio.css'
import DesainCard from '../desain/DesainCard'
import Gambar1 from '../desain/1.png'
import Gambar2 from '../desain/2365.jpg'
import Gambar3 from '../desain/alfie.png'
import Gambar4 from '../desain/balqis.png'
import Gambar5 from '../desain/bedull.jpg'
import Gambar6 from'../desain/BNSP.png'
import Gambar7 from '../desain/coba.png'
import Gambar8 from '../desain/dahlah.jpg'


//*import Boostrap
import 'bootstrap/dist/css/bootstrap.min.css'
import Card from 'react-bootstrap/Card'

//*swiper 
import {Swiper, SwiperSlide} from 'swiper/react'
import {Autoplay, EffectCards, EffectFlip, Navigation, Pagination} from 'swiper/modules'
import 'swiper/css'
import 'swiper/css/effect-cards'
import 'swiper/css/navigation'
import 'swiper/css/effect-flip'
import 'swiper/css/pagination'

//*ref
import {useRef} from 'react'
//*Import Component Boostrap
import Carousel from 'react-multi-carousel'
import 'react-multi-carousel/lib/styles.css'

const Portofolio =() =>{
  
    return(
    <>      
        <div> 
        
        <h3 className='text-center desain'> Desain </h3>
          <div className='container py-4 px-4 justify-content-center'
           
          >
            <Swiper 
            autoplay ={
              {delay: 1500}
            }
           
            rewind={true}
            effect={'cards'}
            grabCursor={true}
            modules={[Autoplay, EffectCards]}
            className='mySwipper'
            spaceBetween={1}
            style={{position: 'center', width: '45%', margin:'auto' }}
            >
              <SwiperSlide>
              <DesainCard data={{ImgSrc: Gambar1, Caption:'Desain Banner', Title:'Desain ini digunakan untuk keperluan PMB Sebuah Perguruan Tinggi' }}/>
              </SwiperSlide>

              <SwiperSlide>
              <DesainCard data={{ImgSrc: Gambar2, Caption:'Desain Vector', Title:'Desain ini digunakan untuk keperluan pribadi' }}/>
              </SwiperSlide>

              <SwiperSlide>
              <DesainCard data={{ImgSrc: Gambar3, Caption:'Desain WPAP', Title:'Desain ini digunakan untuk keperluan pribadi' }}/>
              </SwiperSlide>

              <SwiperSlide>
              <DesainCard data={{ImgSrc: Gambar4, Caption:'Desain Vector', Title:'Desain ini digunakan untuk keperluan pribadi' }}/>
              </SwiperSlide>

              <SwiperSlide>
              <DesainCard data={{ImgSrc: Gambar5, Caption:'Desain Logo', Title:'Desain ini digunakan untuk keperluan bisnis' }}/>
              </SwiperSlide>
              <SwiperSlide>
              <DesainCard data={{ImgSrc: Gambar6, Caption:'Desain Poster', Title:'Desain ini digunakan untuk keperluan Iklan Layanan Masyarakat' }}/>
              </SwiperSlide>
              <SwiperSlide>
              <DesainCard data={{ImgSrc: Gambar7, Caption:'Desain Logo', Title:'Desain ini digunakan untuk keperluan pribadi' }}/>
              </SwiperSlide>
              <SwiperSlide>
              <DesainCard data={{ImgSrc: Gambar8, Caption:'Desain Logo', Title:'Desain ini digunakan untuk keperluan bisnis' }}/>
              </SwiperSlide>
            </Swiper>
        
          </div> 

        <h3 className='text-center video'> Video</h3>
        <div className='container '
        style={{  justifyContent: 'center'
        }}
        >
            <Swiper
            effect={'flip'}
            rewind={true}
            navigation={true}
            pagination={true}
            centeredSlides={true}
            className='mySwipper'
            modules={[Navigation,EffectFlip,Pagination]}
            slidesPerView={'1'}
            spaceBetween={'30'}
            style={{ position:'center', width:'auto', height : 'auto'}}
            >
              <SwiperSlide>
                <div>
                  <ReactPlayer
                  url='.https://www.youtube.com/watch?v=5aLMAVcDJ2I'
                  className='react-player'
                  playing={false}
                  controls={true}
                  width='100%'
                  />
                </div>
              </SwiperSlide>

              
            </Swiper>
            </div>
        </div>
        
    </>
    )
    
    
    }
    
    export default Portofolio;