import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import { Card } from 'react-bootstrap'
import './desaingambar.css'
const DesainCard = props =>{
    let {ImgSrc, Caption, Title} = props.data;
    return(
        <Card 
        className=' p-0 overflow-hidden h-20 shadow' 
       
        >
            <div className=' overflow-hidden rounded p-0'>
                <Card.Img  variant='top' src={ImgSrc} />
            </div>
            <Card.Body className='text-center'>
                <Card.Title className='kartu display-6'>{Caption}</Card.Title>
                <Card.Title className='bawah'>{Title}</Card.Title>
            </Card.Body>
        </Card>
    )
}

export default DesainCard;