import Hadeh from "../desain/profile.png"
import Image from 'react-bootstrap/Image'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import '../css/Profile.css'
import Typed from "react-typed"
import {easeInOut, motion} from 'framer-motion'
const Profile =() =>{
    const item ={
        show :{
            opacity: 100,
            scale: 1,
            y: [-20,0],
            transition:{
                repeat: Infinity,
                repeatType: 'reverse',
                ease: easeInOut,
                duration: 3
            }
        }
    }
    return(
        <Container>
             
            <Row style={{justifyContent:'center'}}>   
                <p className="hi1">Hai,
                <Typed strings={['Saya Muhammad Rizqi Kurniawan',
                'Saya seorang Front-end Developer', 
                'Saya seorang Design Graphic']}
                typeSpeed={100}
                backSpeed={100}
                delay={300}
                loop={true}
                />
                </p>
            </Row>      
                
                
             <Row style={{justifyContent: 'center'}}>   
                <motion.img
                variants={item}
                src={Hadeh}
                style={{
                    width:'15rem'
                }}
              
                animate='show'
             
                />
                
        </Row>
    </Container>
    )
    
    
    }
    
    export default Profile;