import Container from 'react-bootstrap/Container'
import 'animate.css'
import '../css/About.css'
import * as React from 'react'
import {motion, useViewportScroll, useTransform} from 'framer-motion'
const About =() =>{
    const { scrollYProgress } = useViewportScroll();
        const scale = useTransform(scrollYProgress, [0, 3], [1, 0]);
    return(
        <Container>
        <motion.div
        style={{
          scale
        }}
      >
        <p className="aboutku">Saya adalah seorang Front-end developer dan design graphic, saya lulusan Sarjana
        Informatika dari Universitas Nasional. Saya mampu mengaplikasikan bahasa pemograman HTML, CSS, React.js.
        Pada penggunaan CSS dan React.js saya menggunakan Framework Boostrap untuk memperindah tampilan Sebuah
        website. Selain itu saya dapat menggunakan Adobe Illustrator untuk membuat sebuah design graphic dan
        membuat motion graphic dengan menggunakan Adobe After Effect.</p>
<motion.div
          className="item"
          style={{
            scaleY: scrollYProgress
          }}
        />
         </motion.div>
    </Container>
    )
    
    
    }
    
    export default About;